<template>
<div id="myupbox">
<el-form ref="form" :model="form" label-width="120px">
  <el-form-item label="商品名称">
    <el-input v-model="form.name"></el-input>
  </el-form-item>
  <el-form-item label="商品价格">
    <el-input id="myinput" v-model="form.price" placeholder="单位￥/元："></el-input>
  </el-form-item>
  <el-form-item label="商品数量">
    <el-input id="myinput" v-model="form.count" placeholder="请填入整数，不要填一个/一条/一只等等"></el-input>
  </el-form-item>
  <el-form-item label="商品类型">
    <el-select v-model="form.type1" placeholder="商品类型">
      <el-option label="水果" value="fruit"></el-option>
      <el-option label="蔬菜" value="vegetables"></el-option>
      <el-option label="鱼" value="fish"></el-option>
      <el-option label="肉" value="meat"></el-option>
      <el-option label="蜂蜜" value="bee"></el-option>
      <el-option label="菜干" value="pickles"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="特别标志">
      <el-checkbox v-model="form.hot">设为热卖产品</el-checkbox>
      <el-checkbox v-model="form.isnew">设为上市新品</el-checkbox>
  </el-form-item>
  <el-form-item label="商品简要说明">
    <el-input type="textarea" v-model="form.desc"></el-input>
  </el-form-item>
  <el-form-item label="商品第一张图片">
      <input type="file"  ref="input1"  @change= "getpic1base64">
      <img :src="form.pic1base64" alt=""/>
    </el-form-item>
    <el-form-item label="商品第二张图片">
      <input type="file"  ref="input2"  @change= "getpic2base64">
      <img :src="form.pic2base64" alt=""/> 
    </el-form-item>
    <el-form-item label="商品第三章图片">
     <input type="file"  ref="input3"  @change= "getpic3base64">
     <img :src="form.pic3base64" alt=""/>
   </el-form-item>  
  <el-form-item>
    <el-button type="primary" @click="onSubmit">确认修改</el-button>
    <el-button>取消</el-button>
  </el-form-item>
</el-form>
</div>
</template>
<script>
import axios from 'axios';
import { mapGetters, mapActions} from 'vuex'
  export default {
    data() {
      return {
        form: {
         id:"",
         mutationType:'changesinglegoodsinfo',
         price:"",
         count:"",
          pic1base64: '',
          pic2base64: '',
          pic3base64: '',
          name: '',
          type1: '',
          hot: false,
          isnew: true,
          desc: ''
        }
      }
    },
    computed:{
      ...mapGetters([
      'getlocalapi',
       'singlegoods',
    ])},
    methods: {
      onSubmit() {
      if(this.form.price!==""&&this.form.count!==""&&this.form.pic1base64!==''&&this.form.pic2base64!==''&& this.form.pic3base64!== ''&& this.form.name!==''&&this.form.type1!==''&& this.form.desc!== ''){
         axios.post(this.getlocalapi+"/goods",JSON.stringify(this.form)) 
          .then(response => {
            if(response.data.code=="ok"){
              const h = this.$createElement;
              this.$message({
                message: h('p', null, [
                  h('span', null, '加入成功 '),
                  h('i', { style: 'color: teal' }, '')
                ])
              });
              
              this.form.price=""
              this.form.count=""
              this.form.pic1base64= ''
              this.form.pic2base64= ''
              this.form.pic3base64= ''
              this.form.name= ''
              this.form.type1=''
              this.form.hot=false
              this.form.isnew= true
              this.form.desc= ''
            }
          })
          .catch(err => {
           console.log(err);
          }) 
        }else{
          const h = this.$createElement;
              this.$message({
                message: h('p', null, [
                  h('span', null, '数据未填写完整 '),
                  h('i', { style: 'color: teal' }, '')
                ])
              });
        }
       
      },
      getpic1base64(){
        var that=this
        var reader = new FileReader();
        var AllowImgFileSize = 2100000; //上传图片最大值(单位字节)（ 2 M = 2097152 B ）超过2M上传失败
        var file = this.$refs.input1.files[0];
        var imgUrlBase64;
        if (file) {
            //将文件以Data URL形式读入页面  
            imgUrlBase64 = reader.readAsDataURL(file);
            reader.onload = function (e) {
              //var ImgFileSize = reader.result.substring(reader.result.indexOf(",") + 1).length;//截取base64码部分（可选可不选，需要与后台沟通）
              if (AllowImgFileSize != 0 && AllowImgFileSize < reader.result.length) {
                    alert( '上传失败，请上传不大于2M的图片！');
                    return;
                }else{
                    //执行上传操作
                    that.form.pic1base64=reader.result
                   // console.log(reader.result);
                }
            }
        }  
      },
      getpic2base64(){
        var that=this
        var reader = new FileReader();
        var AllowImgFileSize = 2100000; //上传图片最大值(单位字节)（ 2 M = 2097152 B ）超过2M上传失败
        var file = this.$refs.input2.files[0];
        var imgUrlBase64;
        if (file) {
            //将文件以Data URL形式读入页面  
            imgUrlBase64 = reader.readAsDataURL(file);
            reader.onload = function (e) {
              //var ImgFileSize = reader.result.substring(reader.result.indexOf(",") + 1).length;//截取base64码部分（可选可不选，需要与后台沟通）
              if (AllowImgFileSize != 0 && AllowImgFileSize < reader.result.length) {
                    alert( '上传失败，请上传不大于2M的图片！');
                    return;
                }else{
                    //执行上传操作
                    that.form.pic2base64=reader.result
                   // console.log(reader.result);
                }
            }
        }  
      },
      getpic3base64(){
        var that=this
        var reader = new FileReader();
        var AllowImgFileSize = 2100000; //上传图片最大值(单位字节)（ 2 M = 2097152 B ）超过2M上传失败
        var file = this.$refs.input3.files[0];
        var imgUrlBase64;
        if (file) {
            //将文件以Data URL形式读入页面  
            imgUrlBase64 = reader.readAsDataURL(file);
            reader.onload = function (e) {
              //var ImgFileSize = reader.result.substring(reader.result.indexOf(",") + 1).length;//截取base64码部分（可选可不选，需要与后台沟通）
              if (AllowImgFileSize != 0 && AllowImgFileSize < reader.result.length) {
                    alert( '上传失败，请上传不大于2M的图片！');
                    return;
                }else{
                    //执行上传操作
                    that.form.pic3base64=reader.result
                   // console.log(reader.result);
                }
            }
        }  
      },
    },
   created(){
              console.log(this.singlegoods.goodsname);
              this.form.id=this.singlegoods.googsid
              this.form.price=this.singlegoods.googsprice
              this.form.count=this.singlegoods.googscount
              this.form.pic1base64=this.singlegoods.googspic1base64
              this.form.pic2base64= this.singlegoods.googspic2base64 
              this.form.pic3base64= this.singlegoods.googspic3base64
              this.form.name= this.singlegoods.goodsname
              this.form.type1=this.singlegoods.googstype
              this.form.hot=this.singlegoods.googshot
              this.form.isnew= this.singlegoods.googsisnew
              this.form.desc= this.singlegoods.googsintrod
    }
  }
</script>
<style scoped>
#myupbox{
  width:17rem;
  margin-top:0rem;
  margin-left:0.5rem;
}
img{
  height:1.5rem;
  width:2rem;
}
#myinput{
  width:1rem !import;
}
</style>


